import React from "react";

const RouteResult = ({ route, timeAB, endTimeAB, timeBA, endTimeBA, wait, ticket, totalPrice, }) => {

  const changeForms = (number, one, two, five) => {
    let n = Math.abs(number);
    n %= 100;
    if (n >= 5 && n <= 20) {
      return five;
    }
    n %= 10;
    if (n === 1) {
      return one;
    }
    if (n >= 2 && n <= 4) {
      return two;
    }
    return five;
  }

  return (
    <div className="result__container">
      {route === "A-B" ? (
        <div>
          <p>  <b>{ticket} {changeForms(ticket, '', '', '')}</b>   <b>{route}</b>  <b>{totalPrice}</b>.</p>
          <p>     <b>{wait} </b>.</p>
          <p>   <b>{timeAB}</b>,    <b>{endTimeAB}</b>.</p>
        </div>
      ) : route === "B-A" ? (
        <div>
          <p>  <b>{ticket} {changeForms(ticket, '', '', '')}</b>   <b>{route}</b>  <b>{totalPrice}</b>.</p>
          <p>     <b>{wait} </b>.</p>
          <p>   <b>{timeBA}</b>,    <b>{endTimeBA}</b>.</p>
        </div>
      ) : (
        <div>
          <p>  <b>{ticket} {changeForms(ticket, '', '', '')}</b>   <b>{route}</b>  <b>{totalPrice}</b>.</p>
          <p>     <b>{wait[0]}. {wait[1]}.</b></p>
          <p>   A  <b>{timeAB}</b>,   B -  <b>{endTimeAB}</b>.</p>
          <p>   B  <b>{timeBA}</b>,   A -  <b>{endTimeBA}</b>.</p>
        </div>
      )}
    </div>
  );
};

export default RouteResult;