import React from "react";
import { FormControl, InputLabel, MenuItem, Select } from "@mui/material";

const SelectRoute = ({ route, setRoute }) => {
  const dataRoute = [
    { value: "A-B", description: " A  B ( - 700.)" },
    { value: "B-A", description: " B  A ( - 700.)" },
    { value: "A-B-A", description: " A  B     ( - 1200.)" },
  ];

  return (
    <FormControl fullWidth>
      <InputLabel htmlFor="route"></InputLabel>
      <Select
        id="route"
        value={route}
        label=""
        onChange={(e) => setRoute(e.target.value)}
      >
        {dataRoute.map((route) => (
          <MenuItem key={route.value} value={route.value}>{route.description}</MenuItem>
        ))}
      </Select>
    </FormControl>
  );
};

export default SelectRoute;
